@font-face {
  font-family: "Fira Sans";
  src: url("../../fonts/fira-sans-400.woff2") format("woff2"), url("../../fonts/fira-sans-400.woff") format("woff");
  font-style: normal;
  font-weight: 400;
  font-display: swap; }

@font-face {
  font-family: "Fira Sans";
  src: url("../../fonts/fira-sans-500.woff2") format("woff2"), url("../../fonts/fira-sans-500.woff") format("woff");
  font-style: normal;
  font-weight: 500;
  font-display: swap; }

@font-face {
  font-family: "Fira Sans";
  src: url("../../fonts/fira-sans-700.woff2") format("woff2"), url("../../fonts/fira-sans-700.woff") format("woff");
  font-style: normal;
  font-weight: 700;
  font-display: swap; }

html,
body {
  padding: 0;
  margin: 0; }

body {
  min-width: 320px;
  background-color: #ffffff;
  font-size: 17px;
  line-height: 1.7;
  font-family: "Fira Sans", "Arial", sans-serif;
  color: #000000; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.container {
  margin: 0 auto;
  padding: 0 20px;
  max-width: 1142px; }

.header {
  position: absolute;
  width: 100%;
  padding: 32px 0;
  font-weight: 500;
  font-size: 15px; }

.header__container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center; }

.header__tel {
  color: #000000;
  text-decoration: none; }

.header__tel:hover {
  color: #ffc341; }

.header__callback {
  color: #000000;
  text-decoration: none;
  text-transform: uppercase;
  border-bottom: 1px dashed #000000; }

.header__callback:hover {
  color: #ffc341;
  border-color: #ffc341; }
